:root {
  --primary-blue: #184e7d;
  --secondary-blue: #2a6ca8;
  --light-blue: #e6f0f7;
  --dark-blue: #0d3356;
  --sahayatri-orange: #f26841;
  --text-dark: #333333;
  --text-light: #666666;
  --background-light: #F8F9FA;
  --shadow: 0 4px 6px rgba(24, 78, 125, 0.1);
  --transition: all 0.3s ease;
}

body {
  margin: 0;
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: var(--background-light);
}

/* Navigation */
nav {
  background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--shadow);
}

.nav-brand {
  color: var(--sahayatri-orange);
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 800;
  letter-spacing: 0.5px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.nav-links {
  display: flex;
  gap: 2rem;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: var(--transition);
}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

/* Hero Section */
.hero-section {
  background: linear-gradient(135deg, 
    var(--dark-blue) 0%,
    var(--primary-blue) 50%,
    var(--secondary-blue) 100%
  );
  color: white;
  padding: 3rem 2rem;
  text-align: center;
  border-radius: 1.5rem;
  margin: 1.5rem auto;
  max-width: 1800px;
  box-shadow: var(--shadow);
  position: relative;
  overflow: hidden;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0) 100%);
  pointer-events: none;
}

.hero-section h1 {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  font-size: 3rem;
  margin-bottom: 1rem;
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.1);
}

.hero-section h1 span {
  display: inline-block;
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 3rem;
  font-weight: 600;
}

.hindi-text {
  color: var(--sahayatri-orange);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.hero-subtitle {
  font-size: 1.3rem;
  opacity: 0.9;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Search Filters */
.search-filters {
  background: white;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: var(--shadow);
  margin: 1.5rem auto;
  max-width: 1800px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  position: relative;
  border: 1px solid rgba(24, 78, 125, 0.1);
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.filter-group label {
  font-weight: 800;
  color: var(--primary-blue);
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 0.25rem;
}

.form-input {
  padding: 1rem;
  border: 2px solid rgba(24, 78, 125, 0.1);
  border-radius: 0.8rem;
  font-size: 1rem;
  transition: var(--transition);
  background-color: var(--light-blue);
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 3px rgba(24, 78, 125, 0.1);
  background-color: white;
}

.filter-buttons {
  display: flex;
  gap: 1rem;
  align-items: flex-end;
}

/* Buttons */
.btn {
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
}

.btn-primary {
  background: linear-gradient(135deg, 
    var(--dark-blue) 0%,
    var(--primary-blue) 50%,
    var(--secondary-blue) 100%
  );
  color: white;
  position: relative;
  overflow: hidden;
}

.btn-primary::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 100%);
  opacity: 0;
  transition: var(--transition);
}

.btn-primary:hover::after {
  opacity: 1;
}

.btn-secondary {
  background: white;
  color: var(--primary-blue);
  border: 2px solid var(--primary-blue);
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(24, 78, 125, 0.15);
}

/* User Cards */
.users-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  padding: 1.5rem;
  max-width: 1800px;
  margin: 0 auto;
}

.user-card {
  background: white;
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: var(--shadow);
  transition: var(--transition);
  border: 1px solid rgba(24, 78, 125, 0.1);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

.user-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg,
    var(--dark-blue) 0%,
    var(--primary-blue) 50%,
    var(--secondary-blue) 100%
  );
}

.user-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(24, 78, 125, 0.12);
}

.user-info {
  margin-bottom: 0.75rem;
  flex-grow: 1;
}

.user-info h3 {
  color: var(--primary-blue);
  margin: 0 0 0.75rem 0;
  font-size: 1.2rem;
  font-weight: 600;
  border-bottom: 2px solid var(--sahayatri-orange);
  padding-bottom: 0.5rem;
  position: relative;
}

.user-info p {
  margin: 0.4rem 0;
  color: var(--text-light);
  display: flex;
  align-items: center;
  line-height: 1.3;
  font-size: 0.95rem;
}

.user-info strong {
  color: var(--primary-blue);
  margin-right: 0.75rem;
  min-width: 100px;
  font-weight: 400;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.user-contact {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(24, 78, 125, 0.1);
}

.user-contact button {
  width: 100%;
  padding: 0.6rem;
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  background: var(--light-blue);
  color: var(--primary-blue);
  border: 1px solid var(--primary-blue);
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.teams-logo {
  width: 18px;
  height: 18px;
  object-fit: contain;
}

.user-contact button:hover {
  background: linear-gradient(135deg,
    var(--dark-blue) 0%,
    var(--primary-blue) 50%,
    var(--secondary-blue) 100%
  );
  color: white;
  border-color: transparent;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(24, 78, 125, 0.15);
}

.user-contact button:hover .teams-logo {
  filter: brightness(0) invert(1);
}

/* Loading and Error States */
.loading, .error-message, .no-results {
  text-align: center;
  padding: 3rem;
  color: var(--text-light);
  font-size: 1.2rem;
  background: white;
  border-radius: 1.5rem;
  box-shadow: var(--shadow);
  margin: 2rem auto;
  max-width: 600px;
  border: 1px solid rgba(24, 78, 125, 0.1);
}

.error-message {
  color: #dc3545;
  border: 1px solid rgba(220, 53, 69, 0.1);
}

/* Container */
.find-users-container {
  padding: 1.5rem;
  max-width: 1800px;
  margin: 0 auto;
}

/* Responsive Design */
@media (max-width: 1800px) {
  .users-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 1400px) {
  .users-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1100px) {
  .users-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .search-filters {
    grid-template-columns: 1fr;
    padding: 1.5rem;
  }

  .users-grid {
    grid-template-columns: 1fr;
    padding: 1rem;
  }

  .hero-section {
    padding: 3rem 1.5rem;
  }

  .hero-section h1 {
    font-size: 2.2rem;
  }

  .hero-section h1 span {
    font-size: 2.2rem;
  }

  .hindi-text {
    font-size: 2.2rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .btn {
    padding: 0.8rem 1.5rem;
  }
}
